.lm-form-item {
  display: flex;
  align-items: center;
}
.lm-form-item .label {
  white-space: nowrap;
  text-align: right;
  padding-bottom: 16px;
  margin-right: 15px;
}
.lm-form-item .block {
  flex: 1;
  flex-basis: auto;
}
.lm-form-item .block > .error {
  height: 16px;
}
.lm-form-item .block > .error > span {
  display: inline-block;
  font-size: 12px;
  line-height: 16px;
  height: 16px;
  color: #f56c6c;
}
.lm-form-item .block ::v-deep .el-input .el-input__inner {
  transition: all 0.5s;
}
.lm-form-item .block.isError.isWarning ::v-deep .el-input .el-input__inner {
  border: 1px solid orange;
}
.lm-form-item .block.isError.isWarning .error {
  color: orange;
}
.lm-form-item .block.isError.isWarning .error > span {
  color: orange;
}
.lm-form-item .block.isError ::v-deep .el-input .el-input__inner {
  border: 1px solid #f56c6c;
}
.lm-form-item .block.isError .error {
  color: #f56c6c;
}
.lm-form-item .block .slide-enter-active,
.lm-form-item .block .slide-leave-active {
  transition: all 0.3s ease;
}
.lm-form-item .block .slide-enter,
.lm-form-item .block .slide-leave-to {
  transform: translateX(10px);
  opacity: 0;
}