

















































































































$errorColor: #f56c6c;
$warnColor: orange;
$bottomHeight: 16px;
.lm-form-item {
  display: flex;
  align-items: center;
  .label {
    white-space: nowrap;
    text-align: right;
    padding-bottom: $bottomHeight;
    margin-right: 15px;
  }
  .block {
    flex: 1;
    flex-basis: auto;
    > .error {
      height: $bottomHeight;
      > span {
        display: inline-block;
        font-size: 12px;
        line-height: $bottomHeight;
        height: $bottomHeight;
        color: $errorColor;
      }
    }
    ::v-deep .el-input .el-input__inner {
      transition: all 0.5s;
    }
    &.isError {
      &.isWarning {
        ::v-deep .el-input .el-input__inner {
          border: 1px solid $warnColor;
        }
        & .error {
          color: $warnColor;
          > span {
            color: $warnColor;
          }
        }
      }
      ::v-deep .el-input .el-input__inner {
        border: 1px solid $errorColor;
      }
      & .error {
        color: $errorColor;
      }
    }
    // .slide-enter-active, .slide-leave-active {
    //   transition: opacity .5s
    // }
    // .slide-enter, .slide-leave-active {
    //   opacity: 0;
    //   color: $errorColor;
    // }
    .slide-enter-active,
    .slide-leave-active {
      transition: all 0.3s ease;
    }
    .slide-enter,
    .slide-leave-to {
      transform: translateX(10px);
      opacity: 0;
    }
  }
}
